/* ===== Layout for calculator ===== */
.calc-grid {
  display: grid;
  gap: 24px;
  grid-template-columns: minmax(260px, 1fr) minmax(260px, 1fr);
  grid-template-areas: "left right";
  align-items: start;
}
.calc-left {
  grid-area: left;
}
.calc-right {
  grid-area: right;
  background: #ffffff;
  color: #0f1f3a;
  border-radius: 12px;
  padding: 24px;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.08);
  align-self: start;
  justify-self: center;
  width: 100%;
  max-width: 520px;
}

/* prevent overflow */
.calc-grid > * {
  min-width: 0;
}
.calc-grid select,
.calc-grid input {
  width: 100%;
  max-width: 100%;
  box-sizing: border-box;
}

/* form rows */
.form-row {
  display: grid;
  gap: 12px;
  grid-template-columns: minmax(200px, 1fr) minmax(200px, 1fr);
  align-items: center;
  margin: 8px 0;
}
.form-select {
  width: 100%;
  padding: 10px;
  border-radius: 8px;
  border: 1px solid #ccc;
  background: #fff;
  font-size: 16px;
}

/* addon list */
.addons-wrap {
  display: grid;
  gap: 12px;
  margin-top: 6px;
}
.addon-row {
