.full-banner {
  height: 100vh;
  width: 100%;
  background: linear-gradient(to bottom, #1a1a1a, #15294d, #2d4f8a);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
  position: relative;
  padding: 0 20px;
  padding-top: 120px; /* Adjust to your header height */
}

.banner-content {
  max-width: 700px;
  margin-top: 100px; /* spacing from top header */
}

.banner-content h1 {
  font-size: 3rem;
  font-weight: 700;
  margin-bottom: 20px;
  line-height: 1.2;
}

.banner-content p {
  font-size: 1.2rem;
  margin-bottom: 40px;
}

.banner-button {
  display: inline-block;
  padding: 15px 40px;
  border: 1px solid white;
  border-radius: 5px;
  color: white;
  text-decoration: none;
  font-weight: 500;
  transition: all 0.3s ease;
}

.banner-button:hover {
  background-color: rgba(255, 255, 255, 0.15);
}

/* Layout Container */
.banner-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 80px 20px;
  position: relative;
  z-index: 2;
  gap: 40px;
}

.banner-left {
  flex: 1;
  color: #fff;
  text-align: left;
}

.banner-left h1 {
  font-size: 3rem;
  line-height: 1.1;
  margin-bottom: 20px;
}

.banner-left h1 span {
  display: block; /* Ensures the span starts on a new line */
  margin-top: 30px; /* <--- THIS adds the space you pointed to */

  /* Keep your existing font styles below */
  font-size: 2.5rem;
  font-weight: 500;
  color: #f0f0f0;
}

.banner-trust-badges {
  margin-top: 30px;
  display: flex;
  gap: 20px;
  font-size: 0.9rem;
  font-weight: 500;
}

/* Specialist Card */
.banner-right {
  flex: 0 0 300px;
}

.specialist-card {
  background: #fff;
  padding: 30px;
  border-radius: 12px;
  color: #333;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
}

.specialist-card h3 {
  margin: 20px 0;
  font-size: 1.4rem;
  text-align: center;
}

.form-group {
  margin-bottom: 15px;
  text-align: left;
}

.form-group label {
  display: block;
  font-size: 0.85rem;
  margin-bottom: 5px;
  font-weight: 600;
}

.specialist-card select {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 1rem;
}

.form-submit-btn {
  width: 100%;
  padding: 15px;
  background: #000080; /* Red like Lightspeed */
  color: white;
  border: none;
  border-radius: 50px;
  font-weight: bold;
  font-size: 1.1rem;
  cursor: pointer;
  margin-top: 10px;
}

.form-disclaimer {
  font-size: 0.75rem;
  color: #666;
  margin-top: 15px;
  text-align: center;
}

/* Mobile Responsiveness */
@media (max-width: 900px) {
  .banner-container {
    flex-direction: column;
    text-align: center;
  }
  .banner-left {
    text-align: center;
  }
  .banner-left h1 {
    font-size: 2.5rem;
  }
  .banner-right {
    width: 100%;
    max-width: 400px;
  }
}

/* Form Step Indicator */
.form-steps {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
}

.step {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: 2px solid #ccc;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  font-size: 0.9rem;
  color: #666;
  background: #fff;
}

.step.active {
  border-color: #000080;
  color: #000080;
}

.step.completed {
  background: #000080;
  border-color: #000080;
  color: white;
}

.step-line {
  height: 2px;
  width: 60px;
  background: #ccc;
}

.step-line.filled {
  background: #000080;
}

/* Form Inputs for Step 2 */
.step-content input {
  width: 100%;
  padding: 12px;
  margin-bottom: 10px;
  border: 1px solid #ddd;
  border-radius: 6px;
  box-sizing: border-box;
}

/* Smooth Fade In */
.fade-in {
  animation: fadeIn 0.4s ease-in;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.back-link {
  background: none;
  border: none;
  color: #666;
  font-size: 0.8rem;
  margin-top: 15px;
  cursor: pointer;
  text-decoration: underline;
}

/* Specialist Card Container */
.specialist-card {
  background: #ffffff; /* Explicit white background */
  padding: 30px;
  border-radius: 12px;
  color: #333333; /* Dark text for the labels and titles */
}

/* Fix for Input Visibility */
.form-input,
.form-select {
  width: 100%;
  padding: 12px;
  margin-top: 8px;
  margin-bottom: 15px;
  border: 1px solid #cccccc; /* Visible border */
  border-radius: 6px;
  background-color: #ffffff; /* White background for the box */
  color: #000000 !important; /* DARK BLACK TEXT so you can see what you type */
  font-size: 1rem;
}

/* Ensure placeholder text is visible but slightly lighter */
.form-input::placeholder {
  color: #888888;
}

/* Ensure labels are visible */
.form-group label {
  display: block;
  font-size: 0.9rem;
  font-weight: 600;
  color: #444444;
  text-align: left;
}

/* 1. Desktop Standard (Keeps your original "Full Banner" look) */
.full-banner {
  height: 100vh; /* Forces full screen height on desktop */
  width: 100%;
  background: linear-gradient(to bottom, #1a1a1a, #15294d, #2d4f8a);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
  position: relative;
  padding: 0 20px;
  padding-top: 120px;
  overflow: hidden; /* Keeps things tight on desktop */
}

/* 2. Mobile Specific Fix (Applies only to screens smaller than 900px) */
@media (max-width: 700px) {
  .full-banner {
    height: auto; /* Allow the background to grow with the form */
    min-height: 100vh; /* Ensure it's at least full screen */
    padding-bottom: 60px; /* Space at the bottom so the box doesn't hit the edge */
    display: block; /* Switch from flex to block for better scrolling behavior */
  }

  .banner-container {
    flex-direction: column;
    align-items: center;
    padding: 40px 20px;
    gap: 40px;
  }

  .banner-right {
    width: 100%;
    max-width: 400px; /* Limits width so it doesn't look stretched on tablets */
    margin: 0 auto;
  }

  /* Prevent the contact box from clipping through the photo */
  .specialist-card {
    position: relative;
    z-index: 5;
    margin-bottom: 20px;
  }
}

@media (max-width: 700px) {
  .full-banner {
    /* Adjust this value (e.g., 140px to 180px) until the text is fully visible */
    padding-top: 80px !important;

    height: auto;
    min-height: 100vh;
  }

  .banner-left h1 {
    /* Optionally shrink the font slightly so more text fits on the screen */
    font-size: 2.2rem;
    margin-top: 20px;
  }
}

